import { isSameVNode } from "./index";

function patchProps(el, oldProps = {}, props = {}) {
    // æŸä¸ªå±æ€§ è€èŠ‚ç‚¹ä¸­æœ‰ æ–°èŠ‚ç‚¹ä¸­æ²¡æœ‰ è¦åˆ é™¤è€çš„
    let oldStyles = oldProps.style || {};
    let newStyles = props.style || {};
    // è€èŠ‚ç‚¹æ ·å¼ä¸­æœ‰ æ–°çš„æ²¡æœ‰ è¦æŠŠæ ·å¼å»æ‰
    for(let key in oldStyles) {
        if(!newStyles[key]) {
            el.style[key] = ''
        }
    }

    // æ¯”è¾ƒå…¶ä»–æ–°è€å±æ€§
    for(let key in oldProps) {
        if(!props[key]) {
            el.removeAttribute(key);
        }
    }

    for(let key in props) {
        if(key === 'style') {
            for(let styleName in props['style']) {
                el.style[styleName] = props['style'][styleName];
            }
        } else {
            el.setAttribute(key, props[key]);
        }
    }
}

export function createElm(vnode) {
    let { tag, data, children, text } = vnode;
    // æ ‡ç­¾å…ƒç´ 
    if(typeof tag === 'string') {
        // vnodeå¢åŠ elå±æ€§ï¼Œå°†è™šæ‹Ÿdomå’ŒçœŸå®domå¯¹åº”èµ·æ¥ï¼Œæ–¹ä¾¿åç»­ä¿®æ”¹propså±æ€§
        vnode.el = document.createElement(tag);
        patchProps(vnode.el, {}, data);
        children.forEach(child => {
            const childElm = createElm(child);
            vnode.el.appendChild(childElm);
        })
    } else {
        vnode.el = document.createTextNode(text);
    }
    return vnode.el;
}

// åˆå§‹åŒ–çš„æ—¶å€™ï¼ŒoldVNodeæ˜¯çœŸå®dom æ›´æ–°æ—¶æ˜¯ä¸Šä¸€æ¬¡è™šæ‹ŸDOM
export function patch(oldVNode, vnode) {
    // nodeTypeæ˜¯domå…ƒç´ åŸç”Ÿå±æ€§
    const isRealElement = oldVNode.nodeType;
    if(isRealElement) {
        // è·å–çœŸå®dom
        const elm = oldVNode;
        // è·å–çˆ¶å…ƒç´ 
        const parentElm = elm.parentNode;
        // æ ¹æ®è™šæ‹Ÿdomåˆ›å»ºçœŸå®dom
        let newElm = createElm(vnode);
        // æ’å…¥åˆ°åŸèŠ‚ç‚¹çš„ç›¸é‚»èŠ‚ç‚¹
        parentElm.insertBefore(newElm, elm.nextSibling);
        // ç§»é™¤åŸèŠ‚ç‚¹
        parentElm.removeChild(elm);

        return newElm;
    } else {
        // diffç®—æ³•ï¼ˆå¹³çº§æ¯”è¾ƒï¼‰
        // 1ã€ä¸¤ä¸ªèŠ‚ç‚¹ä¸æ˜¯åŒä¸€ä¸ªèŠ‚ç‚¹ ç›´æ¥åˆ é™¤è€çš„æ¢ä¸Šæ–°çš„ ï¼ˆæ— éœ€å¯¹æ¯”ï¼‰
        // 2ã€ä¸¤ä¸ªèŠ‚ç‚¹æ˜¯åŒä¸€ä¸ªèŠ‚ç‚¹ï¼ˆåˆ¤æ–­tagå’Œkeyï¼‰ æ¯”è¾ƒä¸¤ä¸ªèŠ‚ç‚¹çš„å±æ€§æ˜¯å¦æœ‰å·®å¼‚ï¼ˆå¤ç”¨è€çš„èŠ‚ç‚¹ï¼Œå°†å·®å¼‚çš„å±æ€§æ›´æ–°ï¼‰
        // 3ã€èŠ‚ç‚¹æ¯”è¾ƒå®Œæ¯•åéœ€è¦æ¯”è¾ƒä¸¤ä¸ªèŠ‚ç‚¹çš„å­èŠ‚ç‚¹
        return patchVnode(oldVNode, vnode);
    }
}

function patchVnode(oldVNode, vnode) {
    if(!isSameVNode(oldVNode, vnode)) {
        // æ–°çš„æ›¿æ¢è€çš„
        let el = createElm(vnode);
        oldVNode.el.parentNode.replaceChild(el, oldVNode.el);
        return el;
    }
    // æ–‡æœ¬çš„æƒ…å†µ(æ–‡æœ¬çš„tagå’Œkeyéƒ½ä¸ºundefined) æ¯”è¾ƒæ–‡æœ¬çš„å†…å®¹
    // å¤ç”¨è€èŠ‚ç‚¹çš„å…ƒç´  æ— éœ€æ–°åˆ›å»ºdom
    let el = vnode.el = oldVNode.el;
    // éƒ½æ˜¯æ–‡æœ¬
    if(!oldVNode.tag) {
        if(oldVNode.text !== vnode.text) {
            // ç”¨æ–°çš„æ–‡æœ¬è¦†ç›–è€çš„æ–‡æœ¬
            el.textContent = vnode.text;
        }
    }
    // æ¯”å¯¹æ ‡ç­¾çš„å±æ€§
    patchProps(el, oldVNode.data, vnode.data);

    // æ¯”è¾ƒå„¿å­èŠ‚ç‚¹ éœ€è¦è€ƒè™‘ ä¸€æ–¹æœ‰å„¿å­&ä¸€æ–¹æ²¡å„¿å­ æˆ– ä¸¤æ–¹éƒ½æœ‰å„¿å­
    let oldChildren = oldVNode.children || [];
    let newChildren = vnode.children || [];

    // ä¸¤æ–¹éƒ½æœ‰å„¿å­
    if(oldChildren.length > 0 && newChildren.length > 0) {
        updateChildren(el, oldChildren, newChildren);
    }
    // è€èŠ‚ç‚¹æ²¡æœ‰å„¿å­ æ–°èŠ‚ç‚¹æœ‰ ç›´æ¥æ’å…¥å„¿å­
    else if(newChildren.length > 0) {
        mountChildren(el, newChildren);
    }
    // æ–°èŠ‚ç‚¹æ²¡æœ‰å„¿å­ è€èŠ‚ç‚¹æœ‰ ç›´æ¥åˆ é™¤å„¿å­
    else if(oldChildren.length > 0) {
        // å¯ä»¥å¾ªç¯ removeChild
        el.innerHTML = '';
    }
    return el;
}

function mountChildren(el, newChildren) {
    for(let i = 0; i < newChildren.length; i++) {
        let child = newChildren[i];
        el.appendChild(createElm(child));
    }
}

function updateChildren(el, oldChildren, newChildren) {
    // åŒæŒ‡é’ˆæ¯”è¾ƒä¸¤ä¸ªèŠ‚ç‚¹ æ–°å¤´-æ—§å¤´ æ–°å°¾-æ—§å°¾ å½“å¤´æŒ‡é’ˆè¶…è¿‡å°¾æŒ‡é’ˆæ—¶ç»“æŸ
    let oldStartIndex = 0;
    let newStartIndex = 0;
    let oldEndIndex = oldChildren.length - 1;
    let newEndIndex = newChildren.length - 1;

    let oldStartVnode = oldChildren[oldStartIndex];
    let newStartVnode = newChildren[newStartIndex];

    let oldEndVnode = oldChildren[oldEndIndex];
    let newEndVnode = newChildren[newEndIndex];

    // å°†è€èŠ‚ç‚¹çš„å­èŠ‚ç‚¹åšæ˜ å°„è¡¨
    function makeIndexByKey(children) {
        let map = {};
        children.forEach((child, index) => {
            map[child.key] = index;
        });
        return map;
    }

    let map = makeIndexByKey(oldChildren);

    // æ€è€ƒ å¾ªç¯ä¸ºä»€ä¹ˆè¦åŠ key
    // æ–°è€èŠ‚ç‚¹åŒæ–¹æœ‰ä¸€æ–¹å¤´æŒ‡é’ˆå¤§äºå°¾æŒ‡é’ˆåˆ™åœæ­¢å¾ªç¯
    while(oldStartIndex <= oldEndIndex && newStartIndex <= newEndIndex) {
        // å› ä¸ºå¯èƒ½è¢«æ ‡è®°è¿‡ è®¾ç½®ä¸ºundefined éœ€è¦è·³è¿‡
        if(!oldStartVnode) {
            oldStartVnode = oldChildren[++oldStartIndex];
        }
        // å› ä¸ºå¯èƒ½è¢«æ ‡è®°è¿‡ 
        else if(!oldEndVnode) {
            oldEndVnode = oldChildren[--oldEndIndex];
        }
        // å¤´å¤´æ¯”å¯¹
        else if(isSameVNode(oldStartVnode, newStartVnode)) {
            patchVnode(oldStartVnode, newStartVnode);
            oldStartVnode = oldChildren[++oldStartIndex];
            newStartVnode = newChildren[++newStartIndex];
        }
        // å°¾å°¾æ¯”å¯¹
        else if(isSameVNode(oldEndVnode, newEndVnode)) {
            patchVnode(oldEndVnode, newEndVnode);
            oldEndVnode = oldChildren[--oldEndIndex];
            newEndVnode = newChildren[--newEndIndex];
        }
        // å°¾å¤´æ¯”å¯¹
        else if(isSameVNode(oldEndVnode, newStartVnode)) {
            patchVnode(oldEndVnode, newStartVnode);
            // æ–°çš„å¤´èŠ‚ç‚¹ ä¸ è€çš„å°¾èŠ‚ç‚¹ ç›¸åŒï¼Œå¤ç”¨è€èŠ‚ç‚¹ ç§»åˆ°å¤´éƒ¨ 
            el.insertBefore(oldEndVnode.el, oldStartVnode.el);
            oldEndVnode = oldChildren[--oldEndIndex];
            newStartVnode = newChildren[++newStartIndex];
        }
        // å¤´å°¾æ¯”å¯¹
        else if(isSameVNode(oldStartVnode, newEndVnode)) {
            patchVnode(oldStartVnode, newEndVnode);
            // æ–°çš„å°¾èŠ‚ç‚¹ ä¸ è€çš„å¤´èŠ‚ç‚¹ ç›¸åŒï¼Œå¤ç”¨è€èŠ‚ç‚¹ ç§»åˆ°å°¾éƒ¨
            el.insertBefore(oldStartVnode.el, oldEndVnode.el.nextSibling);
        }
        // ä¹±åºæ¯”å¯¹
        // åœ¨ç»™åŠ¨æ€åˆ—è¡¨æ·»åŠ keyçš„æ—¶å€™ è¦å°½é‡é¿å…ä½¿ç”¨ç´¢å¼• å› ä¸ºç´¢å¼•å‰åéƒ½æ˜¯ä»0å¼€å§‹ ä¼šå‘ç”Ÿé”™è¯¯å¤ç”¨
        // æ ¹æ®è€çš„èŠ‚ç‚¹åˆ—è¡¨åšä¸€ä¸ªæ˜ å°„å…³ç³» ç”¨æ–°èŠ‚ç‚¹å»éå†å¯»æ‰¾ è‹¥æ‰¾åˆ°åˆ™ç§»åŠ¨è‡³å¤´éƒ¨ï¼ˆå› ä¸ºæ–°èŠ‚ç‚¹ä»å¤´éƒ¨éå†ï¼‰ æ‰¾ä¸åˆ°åˆ™æ·»åŠ  æœ€åå¤šä½™çš„è€èŠ‚ç‚¹åˆ™åˆ é™¤
        else {
            let moveIndex = map[newStartVnode.key];
            // åœ¨è€èŠ‚ç‚¹æ˜ å°„è¡¨ä¸­æ‰¾åˆ°å¯¹åº”è™šæ‹ŸèŠ‚ç‚¹ åˆ™å¤ç”¨
            if(moveIndex !== undefined) {
                let moveVnode = oldChildren[moveIndex];
                el.insertBefore(moveVnode.el, oldStartVnode.el);
                // è¡¨ç¤ºè¯¥èŠ‚ç‚¹å·²åšç§»åŠ¨å¤„ç† ä¸èƒ½å†æ¯”è¾ƒå¤ç”¨
                oldChildren[moveIndex] = undefined;
                patchVnode(moveVnode, newStartVnode);
            }
            // æ‰¾ä¸åˆ°åˆ™æ·»åŠ è‡³å¤´éƒ¨
            else {
                el.insertBefore(createElm(newStartVnode), oldStartVnode.el);
            }
            // å½“å‰æ–°èŠ‚ç‚¹å¤„ç†å®Œæ¯• åˆ°ä¸‹ä¸€ä¸ª
            newStartVnode = newChildren[++newStartIndex];
        }
    }
    // è€èŠ‚ç‚¹åˆ—è¡¨éå†å®Œæ¯• æ–°èŠ‚ç‚¹åˆ—è¡¨è¿˜æœªéå†å®Œæ¯• åˆ™æŠŠå¤šä½™çš„æ–°èŠ‚ç‚¹æ’å…¥
    if(newStartIndex <= newEndIndex) {
        for(let i = newStartIndex; i <= newEndIndex; i++) {
            let childEl = createElm(newChildren[i]);
            // è¿™é‡Œå¯èƒ½å‘åè¿½åŠ  ä¹Ÿå¯èƒ½æ˜¯å‘å‰è¿½åŠ ï¼ˆå°¾å°¾æ¯”å¯¹çš„æƒ…å†µï¼‰
            // åˆ¤æ–­æ–¹æ³•æ˜¯ æ–°èŠ‚ç‚¹åˆ—è¡¨endä¸‹æ ‡åæ˜¯å¦æœ‰ä¸‹ä¸€ä¸ªèŠ‚ç‚¹ æœ‰åˆ™å¤´æ’ æ— åˆ™å°¾æ’ 
            // æ‰€ä»¥ä½¿ç”¨insertBeforeä»£æ›¿appendChild å®ƒç¬¬äºŒä¸ªå‚æ•°ä¸ºnullæ—¶ç›¸å½“äºappendChild
            let anchor = newChildren[newEndIndex + 1] ? newChildren[newEndIndex + 1].el : null;
            el.insertBefore(childEl, anchor);
        }
    }
    // æ–°èŠ‚ç‚¹åˆ—è¡¨éå†å®Œæ¯• è€èŠ‚ç‚¹åˆ—è¡¨è¿˜æœªéå†å®Œæ¯• åˆ™æŠŠå¤šä½™çš„è€èŠ‚ç‚¹åˆ é™¤
    if(oldStartIndex <= oldEndIndex) {
        for(let i = oldStartIndex; i <= oldEndIndex; i++) {
            // æœªè¢«åšç§»åŠ¨å¤„ç†çš„è€èŠ‚ç‚¹æ‰åˆ é™¤
            if(oldChildren[i]) {
                let childEl = oldChildren[i].el;
                el.removeChild(childEl);
            }
        }
    }
}